.left-sidebar {
  position: sticky;
  top: 100px; 
  width: 280px;
  background: white;
  z-index: 100;
  overflow-y: auto;
  flex-shrink: 0; 
}

@media (max-width: 1024px) {
  .left-sidebar {
    position: fixed;
    left: -280px;
    top: 0;
    height: 100vh;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
    transform: translateX(-100%);
  }

  .left-sidebar--open {
    left: 0;
    transform: translateX(0);
  }
}

@media (max-width: 768px) {
  .left-sidebar {
    width: 100%;
    left: -100%;
  }
}

/* ===== ОВЕРЛЕЙ ===== */
.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 99;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(2px);
}

.sidebar-overlay--visible {
  opacity: 1;
  visibility: visible;
}

.burger-menu {
  display: none;
  flex-direction: column;
  gap: 4px;
  cursor: pointer;
  border: none;
  background: none;
  z-index: 101;
}

.burger-menu__line {
  width: 24px;
  height: 2px;
  background: var(--color-white);
  border-radius: 2px;
  transition: all 0.3s ease;
}

@media (max-width: 1024px) {
  .burger-menu {
    display: flex;
  }
}

.left-sidebar__nav {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 24px;
}

.left-sidebar__section {
  margin-bottom: 24px;
}

.left-sidebar__section:last-child {
  margin-bottom: 0;
}

.left-sidebar__item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  color: var(--color-gray);
  text-decoration: none;
  border-radius: 8px;
  transition: all 0.3s ease;
  margin-bottom: 4px;
  font-size: 14px;
  font-weight: 500;
  background: none;
  border: none;
  cursor: pointer;
  width: 100%;
  text-align: left;
}

.left-sidebar__item:hover {
  background: var(--color-light-blue);
}

.left-sidebar__item--active {
  background: var(--color-primary);
  color: white;
}

.left-sidebar__item--active:hover {
  background: var(--color-primary);
  color: white;
}

.left-sidebar__item-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
}

.left-sidebar__item-icon svg {
  width: 24px;
  height: 24px;
  stroke: currentColor;
  fill: none;
  stroke-width: 0.1;
}

.left-sidebar__item-count {
  background-color: var(--color-primary);
  color: white;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: 600;
  margin-left: auto;
  min-width: 20px;
}

.left-sidebar__button {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 12px 16px;
  background: var(--color-primary);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 500;
  width: 100%;
  text-align: left;
}

.left-sidebar__button:hover {
  background: var(--color-primary);
  color: var(--color-white);
  transform: translateY(-1px);
}

.left-sidebar__button--add-cargo, .left-sidebar__button--add-transport {
  background: var(--color-light-blue);
  color: var(--color-primary);
}

.left-sidebar__button--active {
  background-color: var(--color-primary);
  color: white;
  transform: translateY(-1px);
}

.left-sidebar__button--active:hover {
  background-color: var(--color-primary);
  color: white;
}

.left-sidebar__button-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  flex-shrink: 0;
}

.left-sidebar__button-icon svg {
  width: 20px;
  height: 20px;
  stroke: currentColor;
  fill: none;
  stroke-width: 1.5;
}

.left-sidebar__divider {
  width: 100%;
  height: 1px;
  background: #E5E7EB;
  border: none;
  margin: 24px 0;
}

.left-sidebar__item--logout {
  color: #DC2626;
  background: #FEF2F2;
}

.left-sidebar__item--logout:hover {
  background: #FEE2E2;
  color: #B91C1C;
  border-color: #FCA5A5;
}

.left-sidebar__item--logout .left-sidebar__item-icon svg {
  stroke: currentColor;
}

@media (max-width: 1024px) {
  .left-sidebar {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .left-sidebar {
    width: 100%;
    transform: translateX(-100%);
    transition: transform 0.3s ease;
    top: 64px; 
  }
  
  .left-sidebar--open {
    transform: translateX(0);
  }
}

.left-sidebar::-webkit-scrollbar {
  width: 6px;
}

.left-sidebar::-webkit-scrollbar-track {
  background: #F3F4F6;
  border-radius: 3px;
}

.left-sidebar::-webkit-scrollbar-thumb {
  background: #D1D5DB;
  border-radius: 3px;
}

.left-sidebar::-webkit-scrollbar-thumb:hover {
  background: #9CA3AF;
}
